import { Component, OnInit } from '@angular/core';
import { NavController, ModalController,MenuController } from '@ionic/angular';
import { BottomSheetPage } from '../bottom-sheet/bottom-sheet.page';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
})
export class DashboardPage implements OnInit {

  constructor(public navCtrl: NavController
    ,public model:ModalController,
    public menuCtrl :MenuController) { 

  }

  ngOnInit() {
  }

  goToTransactionHistoryChecking() {
    this.navCtrl.navigateForward("transaction-history-checking");
  }
  goToTransactionHistoryCD() {
    this.navCtrl.navigateForward("transaction-history-cd");
  }
  goToVeloCD() {
    this.navCtrl.navigateForward("history-velo-cd");
  }

  goToMyVelo() {
     this.navCtrl.navigateForward("history-velo-cd");
  }

  async openModel(){
    const myModel =await this.model.create({
      component: BottomSheetPage,
      cssClass: 'my-custom-modal-css'
    });
 
    await myModel.present();
   }
   ionViewWillEnter() {
    this.menuCtrl.enable(true);
  }
   toggleDrawer(){
    this.menuCtrl.toggle(); 

   }
}
